import React, {useState, useEffect} from "react";
import logoImg from "../../assets/images/logo.svg";
import landingImg from "../../assets/images/landing.svg";
import studyIcon from "../../assets/images/icons/study.svg";
import giveClassesIcon from "../../assets/images/icons/give-classes.svg";
import purpleHeartIcon from "../../assets/images/icons/purple-heart.svg";
import {Link} from "react-router-dom";
import api from "../../services/api";
import "./styles.css";

// Landing component is responsible to show our application homepage.
function Landing() {
    // Using state looking for consumming our api to get the total num of connections
    const [totalConnections, setTotalConnections] = useState(0);

    useEffect(() => {
        api.get("/connections").then((response) => {
            const {total} = response.data;
            setTotalConnections(total);
        });
    }, []);

    return (
        <div id="page-landing">
            <div id="page-landing-content" className="container">
                <div className="logo-container">
                    <img src={logoImg} alt="Proffy logo" />
                    <h2>Sua plataforma de estudos online</h2>
                </div>
                <img
                    src={landingImg}
                    alt="Pataforma de estudos"
                    className="hero-image"
                />
                <div className="buttons-container">
                    {/* Creating a link to our /study page via Routes */}
                    <Link to="/study" className="study">
                        <img src={studyIcon} alt="Estudar" />
                        Estudar
                    </Link>
                    {/* Creating a link to our /give-classes page via Routes */}
                    <Link to="/give-classes" className="give-classes">
                        <img src={giveClassesIcon} alt="Dar aulas" />
                        Dar aulas
                    </Link>
                </div>
                {/* The span below shows the total number of user-made connections  */}
                <span className="total-connections">
                    Total de {totalConnections} conexões já realizadas
                    <img src={purpleHeartIcon} alt="Coração roxo" />
                </span>
            </div>
        </div>
    );
}

export default Landing;
